
import { defineComponent, computed, ref } from "vue";
import { useStore } from "@/use/useStore";
import { usePost } from "@/use/usePost";
import { useAmountInWords } from "@/use/useAmountInWords";
import { useBackHandler } from "@/use/useBackHandler";
import { AllActionTypes } from "@/store/action-types";
import { POSTS } from "@/store/modules/counter/actions";
// import {
//     mapStores
// } from 'vuex';
import { Store } from "@/store";
import { SampleComponent } from "shubh-vue-component-library";
export default defineComponent({
  name: "Network",
  components: {
    SampleComponent
  },
  setup() {
    const store: Store = useStore();
    const amt = ref<string | undefined>();

    const { selectEmployee, empSelected } = usePost();
    const apiDetails = computed((): POSTS[] => store.getters.getPosts);
    const isFetching = ref<boolean>(true);
    store.dispatch(AllActionTypes.FETCH_EMPLOYEES).then(() => {
      isFetching.value = false;
    });

    function incrementCount(elem: POSTS) {
      const { amount } = useAmountInWords(elem.id);
      amt.value = amount;
    }

    const { addBackhandler, removeBackhandler } = useBackHandler();

    return {
      apiDetails,
      isFetching,
      selectEmployee,
      empSelected,
      amt,
      incrementCount,
      removeBackhandler,
      addBackhandler
    };
  },
  unmounted() {
    this.removeBackhandler();
  },
  mounted() {
    this.addBackhandler();
  }
});
